import React, { Component, Fragment } from 'react'
import { Row, Col, Tabs, Tab, Panel, Table } from 'react-bootstrap'
import base from '../base'
//import data from '../data/data.json'

export default class Tabela extends Component {
    constructor(props) {
        super(props)

        this.state = {
            times: false,
            meta: false
        }
    }

    componentDidMount() {
        //this.setState({ times: data.times, meta: data.meta })
        //console.log(this.state.times)
        
        base.bindToState('nervosao/times', {
            context: this,
            state: 'times',
            asArray: true,
            queries: {
                orderByChild: 'posicao'
            }
        })

        base.bindToState('nervosao/meta', {
            context: this,
            state: 'meta'
        })
    }

    render() {
        return (
            <Row>
                <Col xs={12} md={12}>
                    <Tabs defaultActiveKey={1} id="tabs">
                        <Tab eventKey={1} title="Nervosão">
                            <br />
                            {this.state.times &&
                                <Panel>
                                    <Panel.Body>
                                        <br />
                                        <Table>
                                            <thead>
                                                <tr>
                                                    <th>#</th>
                                                    <th>Time</th>
                                                    <th>Pts</th>
                                                    <th>Meta {this.state.meta}</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {
                                                    this.state.times.map((time, i) => {
                                                        let meta = time.pontos - this.state.meta
                                                        return (
                                                            <Fragment key={i}>
                                                                {
                                                                    meta < 0 &&
                                                                    <tr key={time.nome}>
                                                                        <td className={time.classe}>{time.posicao}</td>
                                                                        <td><img width={25} alt={time.nome} src={time.img} /> <span className="hidden-xs">{time.nome}</span></td>
                                                                        <td>{time.pontos}</td>
                                                                        <td>{meta > 0 ? `+${meta}` : meta}</td>
                                                                    </tr> 
                                                                }
                                                                
                                                            </Fragment>
                                                        )
                                                    })
                                                }
                                            </tbody>
                                        </Table>
                                    </Panel.Body>
                                </Panel>
                            }
                        </Tab>
                        <Tab eventKey={2} title="Tranquilão">
                            <br />
                            {this.state.times &&
                                <Panel>
                                    <Panel.Body>
                                        <br />
                                        <Table>
                                            <thead>
                                                <tr>
                                                    <th>#</th>
                                                    <th>Time</th>
                                                    <th>Pts</th>
                                                    <th>Meta {this.state.meta}</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {
                                                    this.state.times.map((time, i) => {
                                                        let meta = time.pontos - this.state.meta
                                                        return (
                                                            <Fragment key={i}>
                                                                {
                                                                    meta >= 0 &&
                                                                    <tr key={time.nome}>
                                                                        <td className={time.classe}>{time.posicao}</td>
                                                                        <td><img width={25} alt={time.nome} src={time.img} /> <span className="hidden-xs">{time.nome}</span></td>
                                                                        <td>{time.pontos}</td>
                                                                        <td>{meta > 0 ? `+${meta}` : meta}</td>
                                                                    </tr> 
                                                                }
                                                            </Fragment>
                                                        )
                                                    })
                                                }
                                            </tbody>
                                        </Table>
                                    </Panel.Body>
                                </Panel>
                            }
                        </Tab>
                    </Tabs>
                </Col>
            </Row>
        )
    }
}



