import React, { Component, Fragment } from 'react'
import { Grid } from 'react-bootstrap'

import Menu from './components/Menu'
import Tabela from './components/Tabela'

import './App.css'

class App extends Component {

  render() {
    return (
      <Fragment>
        <Menu />
        <Grid>
          <br />
          <Tabela />
        </Grid>
      </Fragment>
    );
  }
}

export default App


