import React, { Component } from 'react'
import { Navbar, Nav, NavItem } from 'react-bootstrap'

export default class Menu extends Component {
    render() {
        return (
            <Navbar style={{ backgroundColor: '#FFF' }}>
                <Navbar.Header>
                    <Navbar.Brand>
                        <img width={90} height={90} src="img/logo45.jpg" alt="thumbnail" />
                    </Navbar.Brand>
                    <Navbar.Toggle />
                </Navbar.Header>
                <Navbar.Collapse>
                    <Nav pullRight>
                        <NavItem>
                            <iframe id="twitter-widget-0" scrolling="no" frameBorder="0" allowtransparency="true" className="twitter-follow-button twitter-follow-button-rendered" title="Twitter Follow Button" src="https://platform.twitter.com/widgets/follow_button.08b626208bff56b0ed982833346043ef.pt.html#dnt=false&amp;id=twitter-widget-0&amp;lang=pt&amp;screen_name=podcast45&amp;show_count=false&amp;show_screen_name=true&amp;size=l&amp;time=1527647187304" style={{ "position": "static", "visibility": "visible", "width": "158px", "height": "28px" }} data-screen-name="podcast45"></iframe>
                        </NavItem>
                    </Nav>
                </Navbar.Collapse>
            </Navbar>
        )
    }
}



